.root {
    a {
        text-decoration: none;
        color: black;
    }

    .Card {
        background-color: rgba(255, 255, 255, 0.7);

        img {
            width: 100%;
            border-radius: 4px;
        }

        p {
            font-size: 0.9rem;
        }
    }

    .Grid {
        padding-top: 1rem;
    }

    img {
        width: 2rem;
        border-radius: 50%;
    }

    h4 {
        display: inline;
        padding-left: 1rem;
    }
}